.nav{
    position: fixed;
    top: 0;
    padding: 10px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all .5s;
}

.nav__contents{
    display:flex;
    justify-content:space-between;
}

.nav__logo{
    position:fixed;
    top:1px;
    left:0;
    width:90px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav__avatar{
    position:fixed;
    right:20px;
    width:30px;
    cursor: pointer;

}

.nav__black{
    background: #111;
}